import { createSlice } from "@reduxjs/toolkit"

const initialState = {
  todos: [
    {id: Math.random(), title: 'Покупка продуктов', selected: false, date: new Date('2023-07-07'), desc: 'Закупить продукты для готовки обеда'},
    {id: Math.random(), title: 'Уборка комнаты', selected: false, date: new Date('2023-07-08'), desc: 'Почистить комнату и вынести мусор'},
    {id: Math.random(), title: 'Запись на спортивные занятия', selected: false, date: new Date('2023-07-09'), desc: 'Записаться на ближайшую тренировку в спортзале'}
  ],
};

const todoSlice = createSlice({
  name: 'todos',
  initialState: initialState,
  
  reducers: {
    addTodo(state, action) {
      console.log('Новый туду успешно добавлен в store')

      const todo = {
        id: Math.random(),
        author: action.payload.author,
        title: action.payload.title,
        date: action.payload.date,
        desc: action.payload.desc,
        selected: false,
      }

      let newTodos = [...state.todos, todo];
      for (let i = 0; i < newTodos.length; i++) {
        for (let j = 0; j < newTodos.length -1; j++) {
          if (newTodos[j].date > newTodos[j + 1].date) {
            const current = newTodos[j];
            const next = newTodos[j + 1];
            newTodos[j] = next;
            newTodos[j + 1] = current;
          }
        }
      }


      state.todos = newTodos;
    },
    removeTodo(state, action){
      state.todos = (state.todos.filter(todo => todo.id !== action.payload));
    },
    toggleTodo(state, action){
      console.log(action.payload)
      state.todos.map(todo => {
        if (todo.id == action.payload){
          todo.selected = !todo.selected;
        }
      })
      
    },
  }
});

export const { addTodo, removeTodo, toggleTodo } = todoSlice.actions;
export default todoSlice.reducer;