import React from 'react';

import Radio from '../UI/Radio/Radio'
import SvgComponent from '../SvgComponent';


import './ToDo.scss'

const ToDo = (props) => {

  function formatDate(date) {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    
    return `${year}-${month}-${day}`;
  }

  function isDateBefore(date) {
    return (
      new Date().setHours(0, 0, 0, 0) >= date.setHours(0, 0, 0, 0)
    );
  }
  
  
  return (
    <div className='todo'>
      <div className="todo__left">
        <div className="todo__checkbox">
          <Radio 
            id={props.id} 
            onChange={(() => { props.onSelect(props.id) })}
          />
        </div>
        <div className="todo__content">
          <div className="todo__name">{props.title}</div>
          <div className="todo__desc">{props.desc}</div>
          <div className={isDateBefore(props.date) ? "todo__date red" : "todo__date"}>
            <div className="todo__date-icon">
              <SvgComponent id="calendar"/>
            </div>
              {formatDate(props.date)}
            </div>
        </div>
      </div>
      <div className="todo__right">
        <button onClick={() => props.onDelete(props.id)} className="todo__delete">
          <SvgComponent id="trash"/>
        </button>
      </div>
    </div>
  );
}

export default ToDo;