import React, { useState } from 'react';

import SvgComponent from '../SvgComponent';
import Button from '../UI/Button/Button'

import AirDatepicker from "air-datepicker";
import "air-datepicker/air-datepicker.css";
import localeEn from "air-datepicker/locale/en";

const TodoListBottom = ({ deleteSelected, addTodo }) => {
  const [isOpen, setIsOpen] = useState('');
  const [title, setTitle] = useState('');
  const [desc, setDesc] = useState('');

  const ref = React.useRef();
  const dpInstance = React.useRef();

  const closeBlock = () => {
    setIsOpen('');
  }

  const openBlock = () => {
    setIsOpen('active');
  }

  const handleTitleChange = (value) =>{
    setTitle(value)
    document.getElementById('todo_title').classList.remove('error');
  }

  const validateForm = () => {
    let isValid = true;
  
    if (!title.trim()) {
      isValid = false;
      document.getElementById('todo_title').classList.add('error');
    } else {
      document.getElementById('todo_title').classList.remove('error');
    }
  
    if (dpInstance.current.selectedDates.length === 0) {
      isValid = false;
      document.querySelector('.ToDoList__add-date').classList.add('error');
    } else {
      document.querySelector('.ToDoList__add-date').classList.remove('error');
    }
  
    return isValid;
  };
  
  

  const handleAddTask = () => {
    if (isOpen === '') openBlock();
    else if (validateForm() === false) { return; }
    
    else{
      addTodo({
        title: title, 
        selected: false, 
        date: dpInstance.current.selectedDates[0], 
        author: 'Иван', 
        desc: desc,
      });

      setTitle('');
      setDesc('');
      closeBlock();

    }
  }

  React.useEffect(() => {
    if (ref.current && !dpInstance.current) {
      dpInstance.current = new AirDatepicker(ref.current, {
        inline: false,
        locale: localeEn,
        startDate: new Date(),

        onSelect() {
          document.querySelector('.ToDoList__add-date').classList.remove('error');
        }
      });
    }
  }, []);

  return (
    <div className='ToDoList__bottom' >
      <div className="ToDoList__bottom__row">
        <button onClick={() => deleteSelected()} className="ToDoList__delete">Remove</button>
        <div className={`ToDoList__add ${isOpen}`}>
          <div className="ToDoList__add-top">
            <input value={title} onChange={(e) => handleTitleChange(e.target.value)} className='ToDoList__add-title' id="todo_title" placeholder='Task name here...' type="text" />
            <input value={desc} onChange={(e) => setDesc(e.target.value)} className='ToDoList__add-description' id="todo_description" placeholder='Description' type="text" />
          </div>
          <div className="ToDoList__add-bottom">
            <div className="ToDoList__add-date__wrapper">
              <Button className="ToDoList__add-date">
                <SvgComponent id="calendar"/> 
                <input type="text" id="datepicker_input" ref={ref} placeholder='Due Date'/>
              </Button>
            </div>
            <Button onClick={closeBlock} className="ToDoList__add-cancel">Cancel</Button>
            <button onClick={handleAddTask} className="ToDoList__add-button">Add Task</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TodoListBottom;
